// import { useState } from "react";
import "./StyleSheets/App.css"
// import Slider from "./Components/Slider";
import Empregistration from "./Components/Emp_Registration";
import Userregistraton from "./Components/User_Registration";
import Navbar from "./Components/Navbar";
import Login from "./Components/Login";
import Homepage from "./Components/Homepage";

import { useState } from "react";
const App = () => {
	const userID = localStorage.getItem("user_id");
	const defaultScreen = userID ? "Homepage" : "Login";
	const [screen, setScreen] = useState(defaultScreen);
	const [isset , setIsSet] = useState("");
	const renderScreen = () => {
		switch (screen) {
			case "Login":
				return <Login setScreen={setScreen} />
				break;
			case "Homepage":
				return <Homepage setScreen={setScreen}  isset= {isset}  setIsSet = {setIsSet}/>
				break;
			case "user":
				return <Userregistraton />
				break;
			case "emp":
				return <Empregistration />
				break;
		}
	}

	return (
		<>
			<Navbar screen={screen} setScreen={setScreen} isset= {isset}  setIsSet = {setIsSet}></Navbar>
			{renderScreen()}

		</>
	);
}

export default App;
