import React, { useState } from 'react';
import "../StyleSheets/Navbar.css";
import Dropdown from 'react-bootstrap/Dropdown';
// import Burger from './Burger';
// import { NavLink  , Outlet} from 'react-router-dom';
import Hamburger from './Burger';
const Navbar = ({ screen, setScreen  ,isset , setIsSet}) => {
  const [title , setTitle] = useState("sign in");
  // console.log(screen);
  return (
    <>
      <div className='header'>
        <div className="navbar_logo">
          <h1 className='Logo'>job<span className='X_css'>X</span>press</h1>
        </div>
        <div className='Page_name'>
          {/* <h1>{screen}</h1> */}
        </div>
        {/* <div className='Userprofile'>
          {screen == "Login" ? <button className='btn btn-light sbtn' onClick={() => setScreen("Registration")}>Sing up</button> : ""}
          {screen == "Registration" ? <button className='btn btn-light sbtn' onClick={() => setScreen("Login")}>Sign in</button> : ""}
        </div> */}
      {
        screen =="Login" ||screen == "user" || screen == "emp" ?
        <Dropdown className='drop'>
          <Dropdown.Toggle variant="light" id="dropdown-basic">
           {screen=="Login"? "sign up":"sign in"}
          </Dropdown.Toggle>
          <Dropdown.Menu>
            <Dropdown.Item onClick={() =>setScreen("user")}>User Registration</Dropdown.Item>
            <Dropdown.Item onClick={() =>setScreen("emp")}>For Employer</Dropdown.Item>
            <Dropdown.Item onClick={() =>setScreen("Login")}>Login?</Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
     : "" }
     
      
     {
      screen == "Homepage" ? <Hamburger setIsSet={setIsSet} isset={isset} /> : ""
     }
      </div>
    </>
  )
}
export default Navbar;